.my-help {
  position: relative;
}
.my-help .helpBox {
  position: relative;
  min-height: 90vh;
  max-width: 450Px;
  display: -webkit-flex;
  display: flex;
  font-size: 14px;
  padding-bottom: 10px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.my-help .helpBox > div {
  position: relative;
  width: 33%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 33px;
}
.my-help .helpBox > div > div {
  width: 1.46667rem;
  height: 1.46667rem;
  position: relative;
  margin-bottom: 10px;
}
.my-help .helpBox > div > div img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.my-help .helpBox > div > div span {
  text-align: right;
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  margin: auto;
  color: #FE5252;
  font-size: 16px;
}
.my-help .helpWarn {
  position: absolute;
  max-width: 450Px;
  bottom: 10px;
  left: 20px;
  font-size: 14px;
  color: #666666;
  margin-right: 33px;
  line-height: 20px;
}
