.message .chat {
  padding: 10px 0;
  overflow: hidden;
}
.message .chat > div {
  line-height: 1.5;
  font-size: 16px;
  color: white;
}
.message .chat span {
  position: relative;
  background-color: #68e3ce;
  color: white;
  padding: 8Px;
  max-width: 70%;
  border-radius: 6Px;
}
.message .chat img {
  height: 1.06667rem;
  margin: 10px;
}
.message .chat .to {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.message .chat .to > img {
  -webkit-order: 1;
          order: 1;
}
.message .chat .to > span:after {
  position: absolute;
  width: 0.8rem;
  height: 0.8rem;
  right: -2px;
  top: 0.37333rem;
  content: '';
  border: 100px;
  background-color: #68e3ce;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  z-index: -1;
}
.message .chat .from {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.message .chat .from > span:after {
  position: absolute;
  width: 0.69333rem;
  height: 0.69333rem;
  left: -2px;
  top: 0.37333rem;
  content: '';
  border: 100px;
  background-color: #68e3ce;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  z-index: -1;
}
.message .bottom-input {
  position: fixed;
  width: 100%;
  max-width: 450Px;
  box-sizing: border-box;
  bottom: 0;
}
.message .bottom-input .speak-input-wrap {
  background-color: white;
}
.message .bottom-input .speak-input-wrap .voice {
  padding: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}
.message .bottom-input .speak-input-wrap .voice > img {
  margin-right: 5px;
  height: 32Px;
}
.message .bottom-input .speak-input-wrap .voice .speak {
  font-size: 15Px;
  margin: 6Px 10px;
  width: 70%;
  height: 32Px;
  line-height: 32Px;
  text-align: center;
  border: 1Px solid #999999;
  border-radius: 4Px;
}
.message .bottom-input .alert {
  -webkit-transition: height 0.3s ease;
  transition: height 0.3s ease;
  border-top: 1Px solid #f2f2f2;
}
.message .bottom-input .alert > img {
  height: 60Px;
  margin: 20Px 0 0 20Px;
}
.message .bottom-input .tip {
  position: absolute;
  right: 20px;
  top: -1.86667rem;
}
.message .bottom-input .tip > img {
  height: 1.33333rem;
}
